    p.mb-4(data-aos="fade") La cinem√°tica, concretamente, estudia los siguientes tipos de movimiento:

    .row.fondo2-t1.align-items-center.py-4.mb-4
      .col-lg-12.col-12.px-lg-5.px-md-5.px-4
        .row.justify-content-center

          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
              img.mb-4.mx-auto(src='@/assets/curso/temas/t1/tarj-ico1.svg', alt='' style="width: 85px;")
              h5 Movimiento rectil√≠neo uniforme
              p Es el fen√≥meno donde los cuerpos se desplazan a velocidades constantes, en linera recta, sin aceleraci√≥n o aceleraci√≥n nula.
          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
              img.mb-4.mx-auto(src='@/assets/curso/temas/t1/tarj-ico2.svg', alt='' style="width: 85px;")
              h5 Movimiento rectil√≠neo uniformemente acelerado
              p Se trata de la variaci√≥n de la velocidad que describe un cuerpo de manera lineal, siendo constante en el espacio-tiempo.
          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
              img.mb-4.mx-auto(src='@/assets/curso/temas/t1/tarj-ico3.svg', alt='' style="width: 85px;")
              h5 Movimiento arm√≥nico simple
              p Este tipo de movimiento es aquel donde los cuerpos se desplazan oscilando de un punto a otro, a manera de vaiv√©n y de forma peri√≥dica, o sea, en intervalos de tiempo iguales.
          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
              img.mb-4.mx-auto(src='@/assets/curso/temas/t1/tarj-ico4.svg', alt='' style="width: 85px;")
              h5 Movimiento parab√≥lico
              p Este combina dos movimientos rectil√≠neos, uno vertical (acelerado uniformemente) y otro horizontal (de velocidad constante).
          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
              img.mb-4.mx-auto(src='@/assets/curso/temas/t1/tarj-ico5.svg', alt='' style="width: 85px;")
              h5 Movimiento circular uniforme
              p Es el movimiento que describe un cuerpo realizando c√≠rculos perfectos, en un espacio determinado, sin que var√≠e la velocidad a trav√©s del tiempo.
          .col-lg-3.col-md-6.mb-4
            .tarjeta.fondo3-t1.p-3.px-4.text-center.h-100
