            h3.mb-0 Primera Ley de Newton

    .row.justify-content-center.mb-3
      .col-lg-7.mb-4(data-aos="fade-right")
        p La Primera Ley de Newton, tambi√©n conocida como 
          b ley de la inercia
          |, plantea que un cuerpo o part√≠cula tiende a permanecer en reposo o en movimiento rectil√≠neo a menos que una fuerza externa cambie su estado.
        p Por lo tanto, la ley postula que un objeto no puede cambiar su posici√≥n original, ya sea en reposo o en movimiento uniforme en l√≠nea recta, a menos que se aplique una fuerza o una serie de fuerzas con una resultante distinta de cero.
      .col-lg-5.col-md-8.col-12.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/t1/img5.svg', alt='')

    .row.justify-content-center.mb-3
      .col-lg-5.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/t1/img6.png', alt='')
      .col-lg-7.col-md-8.col-12.mb-4(data-aos="fade-left")
        p En lo relacionado con la Primera Ley de Newton, tenga en cuenta los siguientes aspectos clave:
        AcordionA(tipo="b" clase-tarjeta="tarjeta bg-1")
          .row(titulo="Rozamiento")
            p Newton tuvo en cuenta el hecho de que los cuerpos en movimiento est√°n constantemente sujetos a 
              b rozamiento
              |, lo que los frena gradualmente.
          .row(titulo="Fuerza de fricci√≥n")
            p El rozamiento es novedad en el concepto anterior, que consideraba que el movimiento o la restricci√≥n de un cuerpo depend√≠an, completamente, de si exist√≠a una fuerza externa aplicada sobre √©l, sin apreciar la fuerza de fricci√≥n.
          .row(titulo="Fuerzas externas")
            p Entonces, un objeto que se mueve a una velocidad constante en l√≠nea recta significa que no hay una fuerza externa neta o, en otras palabras, el objeto en movimiento no se detendr√° naturalmente si no se le aplica ninguna fuerza.
          .row(titulo="Reposo <em>versus</em> interferencia energ√©tica")
            p Se sabe que los objetos en reposo tienen velocidad cero, por lo que cualquier cambio en la velocidad se debe a la aplicaci√≥n de una fuerza o interferencia energ√©tica.

    .tarjeta.bg-3.p-4.mb-5    
      .row.align-items-center
        .col-5.col-md-3.col-lg-2.px-lg-4.mb-4.mb-md-0
          img(src='@/assets/curso/temas/t1/img7.svg', alt='')
        .col-12.col-md-9.col-lg-10
          p.mb-0 En los fen√≥menos de la accidentalidad vial, se puede apreciar esta ley de varias maneras. Por ejemplo, cuando unas personas viajan en un bus y este frena repentinamente, los ocupantes tienden a seguir el movimiento, perdiendo estabilidad sobre la posici√≥n en la que se encuentren, como si sus cuerpos quisieran salir volando por la parte delantera del bus.


    .row.justify-content-start.mb-4
      .col-auto(data-aos="fade")
