            h3.mb-0 Segunda Ley de Newton

    .row.justify-content-center.mb-3
      .col-lg-7.mb-4(data-aos="fade-right")
        p La Segunda Ley de Newton establece que la aceleraci√≥n de un cuerpo se relaciona directamente con la fuerza aplicada y la masa del cuerpo. Esta ley se expresa matem√°ticamente de la siguiente manera: 
          span.t-gold.fw-bold.t-italic F=ma
          |, donde 
          span.t-gold.fw-bold.t-italic F 
          | es la fuerza, 
          span.t-gold.fw-bold.t-italic m 
          | es la masa y 
          span.t-gold.fw-bold.t-italic a 
          | es la aceleraci√≥n.
        p En otras palabras, esta ley se√±ala que un objeto acelerar√° en la misma direcci√≥n y con la misma magnitud que la fuerza aplicada. Tambi√©n es la base de explicaci√≥n de muchos fen√≥menos f√≠sicos, como el movimiento de los planetas alrededor del sol, el movimiento de los proyectiles, el movimiento de los objetos en la Tierra y los movimientos de objetos en un campo gravitacional.
        .bg-3.py-3.px-4
          p La Segunda Ley de Newton tiene una gran influencia en todos los campos de la f√≠sica y la tecnolog√≠a. Esta ley es la que cuantifica a la fuerza como una entidad que tiene un valor determinado por la aceleraci√≥n y la masa del cuerpo que la aplica, lo cual indica que su proporcionalidad est√° directamente relacionada con la materia y el cambio de velocidad cuando incide sobre otra.
          .row.justify-content-center
            .col-md-6.col-lg-5
              .tarjeta.bg-acento-contenido
                .row.justify-content-center.align-items-center.text-white.p-4.py-2
                  img(src='@/assets/curso/temas/t1/formula1.svg', alt='Tarjeta que expone la f√≥rmula de la fuerza: Fuerza (F) = masa (m) por aceleraci√≥n (a).')
      .col-lg-5.col-md-8.col-12.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/t1/img8.svg', alt='')

    p.mb-4(data-aos="fade") Las siguientes son algunas generalidades importantes, que usted debe tener claras, sobre la Segunda Ley de Newton:

    .row.justify-content-center.mb-3
      .col-lg-4.col-md-8.col-12.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/t1/img9.png', alt='')
      .col-lg-8.mb-4(data-aos="fade-left")
        AcordionA(tipo="b" clase-tarjeta="tarjeta bg-1")
          .row(titulo="Fuerza y unidad de masa")
            p En un lenguaje coloquial, la fuerza es igual a la unidad de masa, como constante de la porci√≥n limitada de la materia, multiplicada por la aceleraci√≥n, queriendo indicar que, a mayor aceleraci√≥n, mayor ser√° la fuerza, y en caso de considerar la aceleraci√≥n sobre la fuerza, tendr√° tambi√©n una raz√≥n directamente proporcional, indicando que, a mayor masa, mayor ser√° la fuerza.
          .row(titulo="Segunda Ley de Newton en accidentes de tr√°nsito")
            .col-md-7
              p En accidentes de tr√°nsito, es f√°cil visualizar la manifestaci√≥n de esta ley, basta con la comparaci√≥n del da√±o causado por un veh√≠culo clase cami√≥n a un veh√≠culo clase autom√≥vil viajando a velocidades similares, pero la diferencia de masas es muy notoria, por lo cual se podr√° observar un mayor da√±o en el de menor masa.
            .col-md-5
              img(src='@/assets/curso/temas/t1/acord1.png', alt='')
          .row(titulo="Cambio de velocidad")
            p La masa es directamente proporcional a la fuerza y, a su vez, el cambio de velocidad lo es, de acuerdo con la Segunda Ley de Newton, lo cual se convierte en un principio muy valioso para tener en cuenta, especialmente, en el establecimiento de velocidades de circulaci√≥n por carreteras, adem√°s de otros aspectos.
          .row(titulo="Cantidad de movimiento")
            p Cuando la masa no es constante, no aplica la expresi√≥n F=ma, por lo cual lo que se debe hacer en estos casos es tomar la magnitud f√≠sica denominada cantidad de movimiento, la cual est√° expresada en el producto de la masa por la velocidad de un cuerpo.
            h5.mb-1.t-italic P=mv
            p.mb-1 Donde 
            p.mb-0 
              span.fw-bold.t-italic P = 
              | Cantidad de movimiento
            p.mb-0 
              span.fw-bold.t-italic m = 
              | Masa
            p 
              span.fw-bold.t-italic v = 
              | Velocidad

    .row.justify-content-center
      .col-lg-8.mb-4(data-aos="fade-right")
        AcordionA(tipo="b" clase-tarjeta="tarjeta bg-1")
          .row(titulo="Fuerza neta")
            p Esta equivale al producto de la variaci√≥n de la masa y la velocidad sobre la variaci√≥n del tiempo en el que se desarrolla el fen√≥meno.
            img(src='@/assets/curso/temas/t1/form1.svg', alt='Imagen que muestra la f√≥rmula de la fuerza neta: F (fuerza neta) es igual a Variaci√≥n de m (masa) por v (velocidad), sobre variaci√≥n de t (tiempo).' style="width:150px;")
          .row(titulo="Relaci√≥n fuerza, masa, aceleraci√≥n")
            p Partiendo de esto, se puede hacer una relaci√≥n directa entre la fuerza, la masa y la aceleraci√≥n, sin estimar que la masa sea o no constante. En el evento en que la masa sea constante, la expresi√≥n derivada quedar√≠a as√≠:
            img(src='@/assets/curso/temas/t1/form2.svg', alt='Imagen que muestra la siguiente f√≥rmula: Fuerza neta es igual a m (masa)por variaci√≥n de v (velocidad) sobre variaci√≥n de t (tiempo).' style="width:170px;")
          .row(titulo="Expresi√≥n primordial")
            p Ya con esta derivada, se llega a la expresi√≥n primordial de la Segunda Ley de Newton, que indica:
            img(src='@/assets/curso/temas/t1/form3.svg', alt='Imagen que muestra la f√≥rmula: Fuerza neta es igual a masa por aceleraci√≥n.' style="width:105px;")
          .row(titulo="Cantidad de movimiento, <em>momentum</em>, √≠mpetu o momento lineal")
            p La cantidad de movimiento se conoce por la expresi√≥n #[b #[em p=mv]], que significa que la magnitud vectorial que tiene un cuerpo en movimiento es directamente proporcional al producto de la masa por la velocidad del objeto
      .col-lg-4.col-md-8.col-12.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/t1/img10.png', alt='')


    p.mb-4(data-aos="fade") En relaci√≥n con la conservaci√≥n de la cantidad de movimiento, recuerde estos dos conceptos:

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-10
        .tarjeta.bg-5.p-4
          img(src='@/assets/curso/temas/t1/infog.svg', alt='Infograf√≠a que presenta las definiciones de choque el√°stico y choque inel√°stico en recuadros de texto, acompa√±ados cada uno con la imagen de un veh√≠culo en tal situaci√≥n, as√≠: 1, Choque el√°stico: el momento lineal y la energ√≠a cin√©tica permanecen constantes. Dos part√≠culas de diferente masa, que solo interact√∫an y se mueven la una hacia la otra, a velocidades constantes y diferentes, al encontrarse en colisi√≥n, permanecer√°n constantes la cantidad de movimiento y tambi√©n la energ√≠a cin√©tica, como, por ejemplo, el choque de dos bolas de billar. 2, Choque inel√°stico: Se conserva la cantidad de movimiento y se presenta variaci√≥n en la energ√≠a cin√©tica, lo cual se manifiesta en deformaciones, ruido e incremento de la temperatura. El caso t√≠pico ocurre en las colisiones entre veh√≠culos en accidentes de tr√°nsito, donde, tras el choque, la energ√≠a cin√©tica es menor o nula.')

    .row.justify-content-start.mb-4
