    p.mb-4(data-aos="fade") La Tercera Ley de Isaac Newton se puede comprender, de una manera simple, expresando que, cuando un objeto ejerce una fuerza sobre otro, este √∫ltimo responde con la misma fuerza, en la misma direcci√≥n, pero en el sentido contrario sobre el primero.

